a,a:visited,a:hover,a:active{
    -webkit-backface-visibility:hidden;
    backface-visibility:hidden;
    position:relative;
    transition:0.5s color ease;
    text-decoration:none;
    color: white ;
    font-size:2.5em;
}
a:hover{
    color:white;
}
a.before:before,a.after:after{
    content: "";
    transition:0.5s all ease;
    -webkit-backface-visibility:hidden;
    backface-visibility:hidden;
    position:absolute;
}
a.before:before{
    top:-0.25em;
}
a.after:after{
    bottom:-0.25em;
}
a.before:before,a.after:after{
    height:5px;
    height:0.35rem;
    width:0;
    background:white;
}

a.first:after{
    left:0;
}
a.second:after{
    right:0;
}
a.before:hover:before,a.after:hover:after{
    width:100%;
}
